.sidebar-drag-handle {
  position: absolute;
  right: 4px;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: grab;
  opacity: 0.8;
  transition: opacity 0.2s ease;
  z-index: 20;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 3px;
}

.sidebar-drag-handle:hover {
  opacity: 1;
  background: rgba(0, 0, 0, 0.5);
}

.sidebar-drag-handle:active {
  cursor: grabbing;
}

.sidebar-drag-handle .drag-icon {
  width: 12px;
  height: 12px;
  color: #ccc;
}

.sidebar-delete-button {
  position: absolute;
  top: 4px;
  right: 4px;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 1;
  transition: all 0.2s ease;
  z-index: 25;
  background: rgba(220, 38, 38, 0.8);
  border-radius: 4px;
  border: 1px solid rgba(220, 38, 38, 0.9);
