  cursor: pointer;
  transition: all 0.2s ease;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.edit-subcategories-btn:hover {
  background: #2980b9;
  transform: scale(1.1);
}

.edit-subcategories-btn svg {
  width: 14px;
  height: 14px;
  color: white;
}

/* Drag and Drop Styles */
.play-tile {
  position: relative;
}

.play-tile-drag-handle {
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.6);
  border-radius: 4px;
  padding: 4px;
  cursor: grab;
  z-index: 10;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.play-tile:hover .play-tile-drag-handle {
  opacity: 1;
}

.play-tile-drag-handle:active {
  cursor: grabbing;
}

.drag-icon {
  width: 12px;
  height: 12px;
  color: white;
}

.play-tile-content {
  padding-top: 24px;
}

/* Add New Play Tile */
.add-new-play-tile {
  background: linear-gradient(135deg, #2d5a27, #3d7a37);
  border: 2px dashed #4a9d4a;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 200px;
}

.add-new-play-tile:hover {
  background: linear-gradient(135deg, #3d7a37, #4d9d4d);
  border-color: #5aad5a;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(77, 157, 77, 0.3);
}

.add-new-play-content {
  text-align: center;
  color: white;
}

.add-new-play-icon {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  opacity: 0.8;
}

.add-new-play-text {
  font-size: 1.1rem;
  font-weight: 500;
  opacity: 0.9;
}

/* Play Info Display */
.play-info-display {
  background: #444;
  border: 1px solid #555;
  border-radius: 4px;
  padding: 8px 12px;
  color: #fff;
  font-weight: 500;
  margin-top: 4px;
}

/* Grid Layout for Play Tiles */
.play-tiles-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(var(--grid-rows, 2), auto);
  gap: 1rem;
  padding: 1rem 0;
  max-width: 100%;
  overflow-x: hidden;
}

.center-content {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.formation-header {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #444;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.formation-header h2 {
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 600;
}

/* Subcategory Tabs */
.subcategory-tabs {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.subcategory-tab {
  padding: 0.75rem 1.5rem;
  background: #333;
  border: none;
  border-radius: 6px;
  color: #888;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s ease;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.subcategory-tab:hover {
  background: #444;
  color: #ffffff;
  transform: translateY(-1px);
}

.subcategory-tab:active {
  transform: translateY(0);
  background: #333;
}

.subcategory-tab.active {
  background: #007acc;
  color: #ffffff;
}

/* Play Tiles */
.play-tiles-container {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  max-width: 100%;
}

.play-tiles-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(var(--grid-rows, 2), auto);
  gap: 1rem;
  padding: 0.5rem;
  max-width: 100%;
  overflow-x: hidden;
}

.play-tile {
  background: #333;
  border-radius: 8px;
  overflow: hidden;
