/* Mobile Responsive Styles */
@media (max-width: 768px) {
  /* Header adjustments */
  .app-header {
    padding: 0.75rem 1rem;
  }

  .header-content {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }

  .header-nav-section {
    width: 100%;
    flex-wrap: wrap;
    gap: 0.5rem;
  }

  .team-name {
    font-size: 1.5rem;
  }

  .mode-name {
    font-size: 1.2rem;
  }

  .nav-tabs {
    flex-wrap: wrap;
    gap: 0.25rem;
  }

  .nav-tab {
    padding: 0.5rem 0.75rem;
    font-size: 0.8rem;
  }

  .tab-icon {
    width: 16px;
    height: 16px;
  }

  .defense-switch-btn,
  .global-game-reset-btn {
    padding: 0.5rem 1rem;
    font-size: 0.85rem;
  }

  /* Canvas and field adjustments */
  .football-field-container {
    padding: 0.5rem;
  }

  .football-field-canvas {
    touch-action: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
  }

  .play-designer-canvas {
    touch-action: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
  }

  /* Play tiles grid */
  .play-tiles-grid {
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 1rem;
    padding: 1rem;
  }

  .play-tile {
    min-height: auto;
  }

  /* Modals */
  .modal-content {
    width: 95%;
    max-width: 95%;
    margin: 1rem auto;
    padding: 1rem;
    max-height: 90vh;
    overflow-y: auto;
  }

  /* Forms */
  .form-group {
    margin-bottom: 1rem;
  }

  .form-group label {
    font-size: 0.9rem;
  }

  .form-group input,
  .form-group select,
  .form-group textarea {
    font-size: 16px; /* Prevents zoom on iOS */
    padding: 0.75rem;
  }

  /* Buttons */
  .btn {
    padding: 0.75rem 1.5rem;
    font-size: 0.9rem;
    min-height: 44px; /* Touch-friendly size */
  }

  /* Sidebar */
  .sidebar {
    width: 100%;
    max-width: 100%;
  }

  /* Toolbar */
  .play-designer-toolbar {
    flex-wrap: wrap;
    gap: 0.5rem;
    padding: 0.75rem;
  }

  .toolbar-btn {
    padding: 0.5rem 1rem;
    font-size: 0.85rem;
    min-height: 44px;
  }
}

@media (max-width: 480px) {
  .app-header {
    padding: 0.5rem;
  }

  .team-name {
    font-size: 1.25rem;
  }

  .mode-name {
    font-size: 1rem;
  }

  .nav-tab {
    padding: 0.4rem 0.5rem;
    font-size: 0.75rem;
  }

  .play-tiles-grid {
    grid-template-columns: 1fr;
    padding: 0.5rem;
  }

  .modal-content {
    width: 98%;
    max-width: 98%;
    padding: 0.75rem;
  }

  .play-designer-toolbar {
    flex-direction: column;
  }

  .toolbar-btn {
    width: 100%;
  }
}

/* Touch-friendly interactions */
@media (hover: none) and (pointer: coarse) {
  /* Remove hover effects on touch devices */
  .nav-tab:hover:not(:disabled) {
    color: #888;
    border-bottom-color: transparent;
  }

  .nav-tab.active {
    color: #ffffff;
    border-bottom-color: #007acc;
  }

  /* Increase touch targets */
  button,
  .btn,
  .nav-tab {
    min-height: 44px;
    min-width: 44px;
  }
}